import React from "react";
import { Link, Router } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from 'axios';
import { API_BASE_URL } from "../App";
import { Redirect } from "react-router-dom";
import Login from "./Login";

export default function Register(props){
	const [first_name, setFirstName] = useState("")
	const [last_name, setLastName] = useState("")
	const [email, setEmail] = useState("")
	const [password1, setPassword1] = useState("")
	const [password2, setPassword2] = useState("")

	const [form_invalid, setFormInvalidation] = useState(true)
	
	useEffect(() => {
		if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)){
			if (password1.length >= 8 && /^[A-Za-z]\w{7,14}$/.test(password1)) {
				if (password1 === password2){
					if (first_name.length !== 0 && last_name.length !== 0) {
						setFormInvalidation(false);
					}
					else{setFormInvalidation(true);}
				}
				else{setFormInvalidation(true);}
			}
			else{setFormInvalidation(true);}
		}
		else{setFormInvalidation(true);}

	},[email, password1, password2, first_name, last_name])
	
	
	function saveUser(){

		let user = {'firstName': first_name, 'lastName': last_name, 'email' : email, 'password' : password1 };

		axios.post(API_BASE_URL+"/users/add", user).then(res => {
			if (res.status === 201){
				props.setStatus(201);
			}
			else if (res.status === 208){
				props.setStatus(208);
			}
		});
	}
	
	

    return (
		<>
        <div className="container">
			
			<div className="row main">
				<div className="panel-heading">
	               <div className="panel-title text-center">
	               		<h1 className="title">V-Commerce</h1>
	               		<hr />
	               	</div>
	            </div> 
				<div className="main-login main-center">
					<form className="form-horizontal" method="POST" action="#">
						
						<div className="form-group">
							<label htmlFor="name" className="cols-sm-2 control-label">First Name</label>
							<div className="cols-sm-10">
								<div className="input-group">
									<span className="input-group-addon"><i className="fa fa-user fa" aria-hidden="true"></i></span>
									<input value={first_name} onInput={e => setFirstName(e.target.value)} type="text" className="form-control" name="first_name" id="first_name"  placeholder="Dave"/>
								</div>
							</div>
						</div>

						<div className="form-group">
							<label htmlFor="name" className="cols-sm-2 control-label">Last Name</label>
							<div className="cols-sm-10">
								<div className="input-group">
									<span className="input-group-addon"><i className="fa fa-user fa" aria-hidden="true"></i></span>
									<input value={last_name} onInput={e => setLastName(e.target.value)} type="text" className="form-control" name="last_name" id="last_name"  placeholder="Jones"/>
								</div>
							</div>
						</div>

						<div className="form-group">
							<label htmlFor="username" className="cols-sm-2 control-label">Username</label>
							<div className="cols-sm-10">
								<div className="input-group">
									<span className="input-group-addon"><i className="fa fa-users fa" aria-hidden="true"></i></span>
									<input value={email} onInput={e => setEmail(e.target.value)} type="email" className="form-control" name="username" id="username"  placeholder="Enter your Email"/>
								</div>
							</div>
						</div>

						<div className="form-group">
							<label htmlFor="password" className="cols-sm-2 control-label">Password</label>
							<div className="cols-sm-10">
								<div className="input-group">
									<span className="input-group-addon"><i className="fa fa-lock fa-lg" aria-hidden="true"></i></span>
									<input pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}" value={password1} onInput={e => setPassword1(e.target.value)} type="password" className="form-control" name="password" id="password"  placeholder="Enter your Password"/>
								</div>
							</div>
						</div>

						<div className="form-group">
							<label htmlFor="confirm" className="cols-sm-2 control-label">Confirm Password</label>
							<div className="cols-sm-10">
								<div className="input-group">
									<span className="input-group-addon"><i className="fa fa-lock fa-lg" aria-hidden="true"></i></span>
									<input value={password2} onInput={e => setPassword2(e.target.value)} type="password" className="form-control" name="confirm" id="confirm"  placeholder="Confirm your Password"/>
								</div>
							</div>
						</div>

						<div className="form-group ">
							<button disabled={form_invalid} type="button" className="btn btn-primary btn-lg btn-block login-button" onClick={saveUser}>Register</button>
						</div>
						<div className="login-register">
				            <Link to="/login">Login</Link>
				         </div>
					</form>
				</div>
			</div>
		</div>
		</>
    );
}