import React from 'react'
import PropTypes from 'prop-types';
import {Link, Redirect} from 'react-router-dom';

export default function Header(props) {
  setTimeout(() => {
    props.setStatus();
  }, 4000)
    return (
      <>
      <div id="header-message">
      {
				props.status === 208? <><div className="fixed-top row align-items-center justify-content-center">
				<div className="alert alert-primary d-flex align-items-center" role="alert">
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" className="bi bi-exclamation-triangle-fill flex-shrink-0 me-2" viewBox="0 0 16 16" role="img" aria-label="Warning:">
					<path d="M8.982 1.566a1.13 1.13 0 0 0-1.96 0L.165 13.233c-.457.778.091 1.767.98 1.767h13.713c.889 0 1.438-.99.98-1.767L8.982 1.566zM8 5c.535 0 .954.462.9.995l-.35 3.507a.552.552 0 0 1-1.1 0L7.1 5.995A.905.905 0 0 1 8 5zm.002 6a1 1 0 1 1 0 2 1 1 0 0 1 0-2z"/>
				</svg>
				<div>
					Email already present
				</div>
				</div> 
				</div>
        </>: null
			}
			{
				props.status === 201? <>
        <div className="fixed-top row align-items-center justify-content-center">
				<div className="alert alert-success d-flex align-items-center" role="alert">
				<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" className="bi flex-shrink-0 me-2"  viewBox="0 0 16 16" role="img" aria-label="Success:">
					<path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-3.97-3.03a.75.75 0 0 0-1.08.022L7.477 9.417 5.384 7.323a.75.75 0 0 0-1.06 1.06L6.97 11.03a.75.75 0 0 0 1.079-.02l3.992-4.99a.75.75 0 0 0-.01-1.05z"/>
				</svg>
				<div>
					Registered
				</div>
				</div> 
				</div>
        <Redirect to="/login" />
        </>: null
			}
      </div>
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
        <Link className="navbar-brand" to="/">{props.title}</Link>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <Link className="nav-Link" to="/cart">Cart
              {props.cartValue? <b>[{props.cartValue}]</b>:null}
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-Link" to="/login">Login</Link>
            </li>
          </ul>
        </div>
      </nav>
    </>
    )
}
Header.defaultProps = {
    title : "V-Commerce",
    home : "Home",
}

Header.propTypes = {
    title : PropTypes.string,
    home : PropTypes.string,
    cartValue : PropTypes.number,
}